[type="checkbox"].cmn-toggle {
  position: absolute;
  margin-left: -9999px;
  visibility: hidden; }

[type="checkbox"].cmn-toggle + label {
  display: block;
  position: relative;
  cursor: pointer;
  outline: none;
  user-select: none; }

[type="checkbox"].cmn-toggle-round + label {
  padding: 1px;
  width: 40px;
  height: 20px;
  background-color: #fff;
  background-color: #EFEFEF;
  border-radius: 20px;
  background-color: #EDEDED;
  border-radius: 20px;
  /*box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);*/ }

[type="checkbox"].cmn-toggle-round + label:before,
[type="checkbox"].cmn-toggle-round + label:after {
  display: block;
  position: absolute;
  top: -1px;
  left: 1px;
  bottom: 1px;
  content: ""; }

[type="checkbox"].cmn-toggle-round + label:before {
  border: none;
  right: 2px;
  background-color: #f1f1f1;
  border-radius: 20px;
  transition: background 0.4s, width 0.4s; }

[type="checkbox"].cmn-toggle-round + label:after {
  width: 20px;
  height: 18px;
  background-color: #fff;
  border-radius: 100%;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  -webkit-transform: scale(1);
  /*transition: margin 0.4s;
  -webkit-transition: -webkit-transform .1s ease-in-out;
  transition: -webkit-transform .1s ease-in-out;*/
  -webkit-animation-duration: .25s;
  -webkit-animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
  -webkit-animation-name: switchThumbAnimationOff;
  animation-duration: .25s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  animation-name: switchThumbAnimationOff;
  pointer-events: none; }

[type="checkbox"].cmn-toggle-round:checked + label:after {
  margin-left: 20px;
  /*-webkit-transition: -webkit-transform .1s ease-in-out;
  transition: -webkit-transform .1s ease-in-out;*/
  -webkit-animation-duration: .25s;
  -webkit-animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
  -webkit-animation-name: switchThumbAnimation;
  animation-duration: .25s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
  animation-name: switchThumbAnimation; }

[type="checkbox"].cmn-toggle-round:checked + label:before {
  border: none;
  transform: none;
  width: 38px;
  height: auto;
  /* top: initial; */
  top: -1px;
  left: 1px;
  background-color: #EFEFEF;
  background-color: #8ce196;
  background-color: #FFFD6F;
  background-color: #FFFEA5;
  box-shadow: 0 1px 2px rgba(253, 244, 72, 0.2); }

[type="checkbox"].cmn-toggle-round:disabled + label:before,
[type="checkbox"].cmn-toggle-round:disabled + label:after {
  background-color: #ddd; }

[type="checkbox"].cmn-toggle-round:disabled + label {
  background-color: #ccc;
  display: none; }

[type="checkbox"]:checked:disabled + label:before {
  border-bottom: 1px; }

[type="checkbox"].cmn-toggle-round:disabled + label {
  background-color: #E6E6E6; }

[type="checkbox"].cmn-toggle-round:disabled + label:before,
[type="checkbox"].cmn-toggle-round:disabled + label:after {
  background-color: #fff !important; }

[type="checkbox"].cmn-toggle + label {
  cursor: default; }

@-webkit-keyframes switchThumbAnimation {
  0% {
    width: 20px;
    margin-left: 0px; }
  50% {
    width: 30px;
    margin-left: 10px; }
  100% {
    width: 20px;
    margin-left: 20px; } }

@keyframes switchThumbAnimation {
  0% {
    width: 20px;
    margin-left: 0px; }
  50% {
    width: 30px;
    margin-left: 10px; }
  100% {
    width: 20px;
    margin-left: 20px; } }

@-webkit-keyframes switchThumbAnimationOff {
  0% {
    width: 20px;
    margin-left: 20px; }
  50% {
    width: 30px;
    margin-left: 00px; }
  100% {
    width: 20px;
    margin-left: 0px; } }

@keyframes switchThumbAnimationOff {
  0% {
    width: 20px;
    margin-left: 20px; }
  50% {
    width: 30px;
    margin-left: 00px; }
  100% {
    width: 20px;
    margin-left: 0px; } }
